import React from "react";
import "./Navbar.css";
import { Link, useLocation } from "react-router-dom";
import { IconRoutesPages } from "./IconRoutesPages";

const Navbar = () => {
  const { pathname } = useLocation();
  return (
    <nav
      className="styleNavbar d-flex align-items-end justify-content-center  text-white w-100"
      style={{ height: "4rem", backgroundColor: "var(--primary)" }}
    >
      <ul className="d-flex w-100 justify-content-center align-items-center mb-0 ps-0 w-100 fs-2">
        {IconRoutesPages.map(({ icon, to }) => (
          <>
            <b className={`${pathname === to && "bg-white "}`} />
            <Link
              to={to}
              className={`${
                pathname === to ? " text-black  bg-white " : "text-white"
              }`}
            >
              {icon}
            </Link>
            <c className={`${pathname === to && "bg-white"}`} />
          </>
        ))}
      </ul>
    </nav>
  );
};
export default Navbar;
